import React from 'react';
import Media from 'react-media';
import style from './sixthScreen.module.css';
import control1 from '../../../img/infoImg/control1.svg';
import control2 from '../../../img/infoImg/control2.svg';
import control3 from '../../../img/infoImg/control3.svg';
import control4 from '../../../img/infoImg/control4.svg';

const SixthScreen = () => (
  <div className={style.wrapper}>
    <h2 className={style.header}>Удостоверения и фотографии контролеров</h2>
    <p className={style.text}>
      Предупреждайте мощенничество с помощью галереи фото и удостоверений Ваших
      контролеров на сайте
    </p>
    <Media query={{ maxWidth: 1199 }}>
      {matches =>
        matches && (
          <div className={style.imgWrapper}>
            <img className={style.smallImg} src={control1} alt="control1" />
            <img className={style.smallImg} src={control2} alt="control2" />
            <img className={style.smallImg} src={control3} alt="control3" />
            <img className={style.smallImg} src={control4} alt="control4" />
          </div>
        )
      }
    </Media>
  </div>
);

export default SixthScreen;
